/* eslint-disable react/no-danger */
import {
  selectDetailProject,
  selectSceneSelected,
} from '@containers/Home/selectors';
import React, { memo } from 'react';
import { useSelector } from 'react-redux';
import { HeaderWrapper } from './styled';

export const Header = memo(() => {
  const detailProject = useSelector(selectDetailProject);
  const sceneSelected = useSelector(selectSceneSelected);

  return (
    <HeaderWrapper id="themeHeader">
      <div className="--content">
        <span className="--projectName">{detailProject?.title}</span>
        <span className="--sceneName">{sceneSelected?.title}</span>
      </div>
    </HeaderWrapper>
  );
});

export default Header;
